<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>{{$t("login.prompt")}}</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-flex xs12>
                  <logo/>
                </v-flex>
                <v-form>
                  <v-text-field
                    box
                    prepend-icon="mdi-account"
                    name="name"
                    :label="$t('login.username')"
                    type="text"
                  ></v-text-field>
                  <v-text-field
                    counter
                    box
                    v-model="value"
                    :label="$t('login.password')"
                    loading
                    prepend-icon="mdi-lock"
                    name="password"
                    :append-icon="show ? 'mdi-eye-off' : 'mdi-eye'"
                    :type="show ? 'text' : 'password'"
                    @click:append="show = !show"
                    :messages="[message]"
                  >
                    <v-progress-linear slot="progress" :value="progress" :color="color" height="4"></v-progress-linear>
                  </v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn color="primary" block large to="/" :disabled="value.length < 5">{{$t("login.login")}}</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import Logo from "./Logo";

export default {
  components: {
    Logo
  },
  data: () => ({
    drawer: null,
    value: "",
    show: false
  }),
  props: {
    source: String
  },
  computed: {
    progress() {
      let score = this.value.length * 3;
      if (this.hasUppercaseLetters) {
        score += 10;
      }
      if (this.hasLowercaseLetters) {
        score += 10;
      }
      if (this.hasNumbers) {
        score += 15;
      }
      if (this.hasSymbols) {
        score += 15;
      }
      if (this.areWordsRepeated) {
        score -= 20;
      }
      if (this.areCharactersRepeated) {
        score -= 30;
      }
      if (!this.isLongEnough) {
        score = Math.min(50, score - 20);
      }
      return Math.min(100, Math.max(0, score));
    },
    color() {
      return ["error", "warning", "success"][Math.floor(this.progress / 40)];
    },
    hasLowercaseLetters() {
      return /[a-z]/.test(this.value);
    },
    hasUppercaseLetters() {
      return /[A-Z]/.test(this.value);
    },
    hasNumbers() {
      return /[0-9]/.test(this.value);
    },
    hasSymbols() {
      return /\W+/.test(this.value);
    },
    isLongEnough() {
      return this.value.length >= 8;
    },
    areCharactersRepeated() {
      return /^(.)\1{3,}$/.test(this.value);
    },
    areWordsRepeated() {
      return /^(.{4,}).*\1$/.test(this.value);
    },
    message() {
      if (this.areCharactersRepeated)
        return this.$t("login.password-security.no-same-characters");
      if (this.areWordsRepeated)
        return this.$t("login.password-security.no-repeated-words");
      if (this.value.length < 8)
        return this.$t("login.password-security.not-long-enough");
      if (!this.hasUppercaseLetters || !this.hasLowercaseLetters)
        return this.$t("login.password-security.no-uppercase-lowercase");
      if (!this.hasNumbers || !this.hasSymbols)
        return this.$t("login.password-security.no-numbers-and-special");
      return "";
    }
  }
};
</script>